import './Startpage.css';
import Hero from './Hero/Hero';
import { Suspense, lazy } from 'react';
const Karussell = lazy(() => import('./Karussell/karussellWithText'));
const Faq = lazy(() => import('./FAQ/Faq'));
const Tabs = lazy(() => import('./Tabs/TabsWithText'));
const Price = lazy(() => import('./Price/price'));

function Startpage() {
  return (
    <div className='body'>
      <div id='warum-wir'>
        <Hero />
      </div>
      <div className='main karussell' id='vorlagen'>
        <Suspense>
        <Karussell />
        </Suspense>
      </div>
      <div className='main tabs' id='ablauf'>
        <Suspense>
        <Tabs />
        </Suspense>
      </div>
      <div className="main price">
        <Suspense>
        <Price />
        </Suspense>
      </div>
      <div className='main faq' id='faq'>
        <Suspense>
        <Faq />
        </Suspense>
      </div>
    </div>
  );
}

export default Startpage;
