import { Routes, Route, useNavigate } from 'react-router-dom';
import React, {Suspense, lazy} from 'react';
import Startpage from './Startpage/startpage';
import AboutUs from './AboutUs/AboutUs';
const Form = lazy(() => import('./Form/Form'));
const Impressum = lazy(() => import('./Datenschutz'));
const Datenschutz = lazy(() => import('./Impressum'));

function Router () {
    const navigate = useNavigate();

   return (
    <>
    <Routes>
      <Route path='/' element={<Startpage />} />
      <Route path='/about' element={<AboutUs />} />
      <Route path='/kontaktformular' element={<Suspense fallback={<div style={{height: '100vh', width: '100vw'}}>loading...</div>}><Form navigate={navigate} /></Suspense>} />
      <Route path='/impressum' element={<Suspense fallback={<div style={{height: '100vh', width: '100vw'}}>loading...</div>}><Impressum /></Suspense>} />
      <Route path='/datenschutzerklärung' element={<Suspense fallback={<div style={{height: '100vh', width: '100vw'}}>loading...</div>}><Datenschutz /></Suspense>} />
    </Routes>
    </>
   );
}

export default Router;